//colors
$color_warning: #ff9600;
$white: #fff;
$color_danger: #fb483a;
$color_info: #00b0e4;
$color_success: #18ce0f;
$color_default: #888888;
$btn_font: #333333;

.btn {
  &:focus {
    outline: none !important;
  }
  text-transform: none;
  font-weight: 600;
}

.btn-circle {
  border: none;
  outline: none !important;
  overflow: hidden;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
  padding: 6px !important;
  height: 35px;
  width: 35px;
  box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2) !important;

  i {
    font-size: 18px;
    position: relative;
    /*left: -1px;*/
    line-height: 1;
  }
}
.btn-border-radius {
  border-radius: 25px !important;
}

.btn-link {
  font-weight: bold;
  color: #333;
  @include transition(0.5s);

  &:active,
  &:focus {
    text-decoration: none;
    color: #333;
  }

  &:hover {
    text-decoration: none;
    color: #333;
    background-color: #ddd;
  }
}

.btn-circle-lg {
  border: none;
  outline: none !important;
  overflow: hidden;
  width: 50px;
  height: 50px;
  @include border-radius(50% !important);

  i {
    font-size: 26px !important;
    position: relative !important;
    left: 0px !important;
    top: 3px;
  }
}

.btn:not(.btn-link):not(.btn-circle) {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.16), 0 2px 10px rgba(0, 0, 0, 0.12);
  @include border-radius(2px);
  border: none;
  outline: none;

  &:hover {
    outline: none;
  }

  i {
    font-size: 20px;
    position: relative;
    line-height: 1;
  }

  span {
    position: relative;
    top: -4px;
    margin-left: 3px;
  }
}

