.btn-group,
.btn-group-vertical {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.16), 0 2px 10px rgba(0, 0, 0, 0.12);

  .btn {
    box-shadow: none !important;
    @include border-radius(0);

    .caret {
      position: relative;
      bottom: 1px;
    }
  }

  .btn-group {
    box-shadow: none !important;
  }

  .btn + .dropdown-toggle {
    border-left: 1px solid rgba(0, 0, 0, 0.08) !important;
  }
}
.btn-group.btn-group-justified {
  display: table;
  width: 100%;
  table-layout: fixed;
  border-collapse: separate;
  .btn,
  .btn-group {
    display: table-cell;
    float: none;
    width: 1%;
  }
}
.tblActnBtn {
  background-color: transparent !important;
  box-shadow: none !important;
  color: #161d38;
  padding: 0px 5px;
}
.tblActnBtn:hover {
  background-color: #fff !important;
  box-shadow: none !important;
}
.tblEditBtn {
  box-shadow: none !important;
  color: #6d747d;
  padding: 0px 5px;
}
.tblDeleteBtn {
  box-shadow: none !important;
  color: #e00045;
  padding: 0px 5px;
}

.tblDeleteBtn:hover {
  background-color: #fff !important;
  color: #e00045;
}
.tblEditBtn:hover {
  background-color: #fff !important;
  color: #6d747d;
}
.btn-toggle-dropdown {
  top: 0px !important;
}
.tblEditBtn:focus,
.tblDeleteBtn:focus {
  background-color: transparent !important;
}
.btn-hover {
  width: 130px;
  font-size: 13px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 38px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  moz-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  &:hover {
    background-position: 100% 0;
    moz-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  &:focus {
    outline: none;
  }
  &.color-1 {
    background-image: linear-gradient(
      to right,
      #25aae1,
      #30dd8a,
      #30dd8a,
      #2bb673
    );
    @include box-shadow(0 4px 15px 0 rgba(49, 196, 190, 0.75));
  }
  &.color-2 {
    background-image: linear-gradient(
      to right,
      #f5ce62,
      #e43603,
      #fa7199,
      #e85a19
    );
    @include box-shadow(0 4px 15px 0 rgba(229, 66, 10, 0.75));
  }
  &.color-3 {
    background-image: linear-gradient(
      to right,
      #667eea,
      #764ba2,
      #6b8dd6,
      #8e37d7
    );
    @include box-shadow(0 4px 15px 0 rgba(116, 79, 168, 0.75));
  }
  &.color-4 {
    background-image: linear-gradient(
      to right,
      #fc6076,
      #ef9d43,
      #ef9d43,
      #e75516
    );
    @include box-shadow(0 4px 15px 0 rgba(252, 104, 110, 0.75));
  }
  &.color-5 {
    background-image: linear-gradient(
      to right,
      #0ba360,
      #3cba92,
      #30dd8a,
      #2bb673
    );
    @include box-shadow(0 4px 15px 0 rgba(23, 168, 108, 0.75));
  }
  &.color-6 {
    background-image: linear-gradient(
      to right,
      #6253e1,
      #852d91,
      #a3a1ff,
      #f24645
    );
    @include box-shadow(0 4px 15px 0 rgba(126, 52, 161, 0.75));
  }
  &.color-7 {
    background-image: linear-gradient(
      to right,
      #29323c,
      #485563,
      #2b5876,
      #4e4376
    );
    @include box-shadow(0 4px 15px 0 rgba(45, 54, 65, 0.75));
  }
  &.color-8 {
    background-image: linear-gradient(
      to right,
      #25aae1,
      #4481eb,
      #04befe,
      #3f86ed
    );
    @include box-shadow(0 4px 15px 0 rgba(65, 132, 234, 0.75));
  }
  &.color-9 {
