//@extend-elements
//original selectors
//.product-grid .social li a:after, .product-grid .social li a:before
%product-extend_1 {
  content: attr(data-tip);
  color: #fff;
  background-color: #000;
  font-size: 12px;
  letter-spacing: 1px;
  line-height: 20px;
  padding: 1px 5px;
  white-space: nowrap;
  opacity: 0;
  //Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
  transform: translateX(-50%);
  position: absolute;
  left: 50%;
  top: -30px;
}

//original selectors
//.product-grid .product-discount-label, .product-grid .product-new-label
%product-extend_2 {
  color: #fff;
  background-color: #ef5777;
  font-size: 12px;
  text-transform: uppercase;
  padding: 2px 7px;
  display: block;
  position: absolute;
  top: 10px;
  left: 0;
}

.product-grid {
  font-family: Raleway, sans-serif;
  text-align: center;
  padding: 0 0 72px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
  z-index: 1;
  .product-image {
    position: relative;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.3s ease 0s;
    a {
      display: block;
    }
    img {
      width: 100%;
      height: auto;
    }
  }
  .pic-1 {
    opacity: 1;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.3s ease-out 0s;
  }
  &:hover {
    .pic-1 {
      opacity: 1;
    }
    .pic-2 {
      opacity: 1;
    }
    .social {
      opacity: 1;
      top: 50%;
    }
    .product-content {
      bottom: 0;
    }
    .title a {
      color: #ef5777;
    }
  }
  .pic-2 {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.3s ease-out 0s;
  }
  .social {
    width: 150px;
    padding: 0;
    margin: 0;
    list-style: none;
    opacity: 0;
    //Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
    transform: translateY(-50%) translateX(-50%);
    position: absolute;
    top: 60%;
    left: 50%;
    z-index: 1;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.3s ease 0s;
    li {
      display: inline-block;
      a {
        color: #fff;
        background-color: #333;
        font-size: 16px;
        line-height: 40px;
        text-align: center;
        height: 40px;
        width: 40px;
        margin: 0 2px;
        display: block;
        position: relative;
        //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
        transition: all 0.3s ease-in-out;
        &:hover {
          color: #fff;
          background-color: #ef5777;
          &:after {
            opacity: 1;
          }
          &:before {
            opacity: 1;
          }
        }
        &:after {
          @extend %product-extend_1;
          content: "";
          height: 15px;
          width: 15px;
          //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
          border-radius: 0;
          //Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
          transform: translateX(-50%) rotate(45deg);
          top: -20px;
          z-index: -1;
        }
        &:before {
          @extend %product-extend_1;
        }
      }
    }
  }
  .product-discount-label {
    @extend %product-extend_2;
    background-color: #333;
    left: auto;
    right: 0;
  }
  .product-new-label {
    @extend %product-extend_2;
  }
  .rating {
    color: #ffd200;
    font-size: 12px;
    padding: 12px 0 0;
    margin: 0;
    list-style: none;
    position: relative;
    z-index: -1;
    li.disable {
      color: rgba(0, 0, 0, 0.2);
    }
  }
  .product-content {
    background-color: #fff;
    text-align: center;
    padding: 10px 0;
    margin: 0 auto;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -27px;
    z-index: 1;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.3s;
  }
  .title {
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.5px;
    text-transform: capitalize;
    margin: 0 0 10px;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.3s ease 0s;
    a {
      color: #828282;
      &:hover {
        color: #ef5777;
      }
    }
  }
  .price {
    color: #333;
    font-size: 17px;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    letter-spacing: 0.6px;
    margin-bottom: 8px;
    text-align: center;
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all 0.3s;
    span {
      color: #999;
      font-size: 14px;
      font-weight: 400;
      text-decoration: line-through;
      margin-left: 3px;
      display: inline-block;
    }
  }
  .add-to-cart {
    color: #000;
    font-size: 14px;
    font-weight: 600;
  }
}
@media only screen and (max-width: 990px) {
  .product-grid {
    margin-bottom: 30px;
  }
}
