  height: 30px;
  width: 60px;
  cursor: pointer;
}

tr.group,
tr.group:hover {
  background-color: #ddd !important;
}
.tableBody {
  font-size: 13px;
  color: #555;
  padding: 0px 15px 0px 15px;
}
.tbl-checkbox {
  vertical-align: bottom !important;
  text-align: center;
}
